import React from 'react';

import {  Icon } from 'rsuite';

//Componentes de roteamento
import { Link } from "react-router-dom";

import logoEIC from "../assets/logo.jpg";

function renderLogo(){
    return <img scr={logoEIC} alt='logo' />
}

export default class Infos extends React.Component {
    render() {
            return (
                <div className="dialogo col">
                    <div className='row center'>
                        <div className='dialogoFixo bkColor2'>
                            <h1 className='Color1 Title' >{renderLogo()} </h1>
                            
                        </div>
                    </div>
                    <div className='row center'>
                        <div className='dialogoContent bkColor2 Color2'>
                            <div className="dialogoMain col-lg center fullWidth center">
                                <div className="col l6">
                                    {this.props.col1} 
                                </div>
                                <div className="col l6">
                                    {this.props.col2} 
                                </div>
                            </div>
                            <div className=" dialogoRodape row center fullWidth end">
                                <Link to="/"><h6 className='iconShare'><Icon className='iconShare' icon='back-arrow' size='1x'/> VOLTAR</h6></Link>
                            </div>
                        </div>
                        
                    </div>
                        <div  className='dialogoNav row distributed2'>
        Logos dos patrocionadores                
                            
                        </div>
                </div>
            );
    }
}