import React from 'react';


export default class Vitoria extends React.Component {
    render() {
            return (
                <div className="dialogo col">
                    <div className='row center'>
                        <div className='dialogoContent opacidade bkColor1 Color4'>
                            <div className="dialogoMainVitoria bkColor3 col-md center fullWidth">
                                <div className="col m6">
                                    {this.props.col1} 
                                </div>
                                <div className="col m6">
                                    {this.props.col2} 
                                </div>
                                <div className="divider">
                                </div>
                            </div>
                        </div>
                        
                    </div>
                </div>
            );
    }
}