import React from 'react';

import { Button, Icon } from 'rsuite';

//Componentes de roteamento
import { Link } from "react-router-dom";

import logoEIC from "./../assets/logo.jpg";


console.log(logoEIC);

function renderLogo(){
    return <img scr={logoEIC} alt='logo'></img>
}


export default class Inicio extends React.Component {
    render() {
        return (
            <div className="dialogo col">
                <div className='row center'>
                    <div className='dialogoFixo bkColor2'>
                        {renderLogo()}
                        {/* /app/CPv2/static/media/logo.96128386.jpg */}
                        <img scr="http://localhost:3000/app/CPv2/static/media/logo.96128386.jpg" alt='logo'></img>
                        <div className="logo">

                        </div>
                    </div>
                </div>
                <div className='row center'>
                    <div className='dialogoContent bkColor2 Color2'>
                    <div className="dialogoMain col center fullWidth center">
                        <div className="row center fullWidth">
                            <h1 className='Color3' >Caça Palavras</h1>
                        </div>
                        <div className="row center fullWidth">
                            <h1 className='Color1 margin-min'>Doenças Tropicais</h1><br/>
                        </div>
                        <div className="row center fullWidth">
                            <Link to="/instrucao1"><Button className='sizeButton borColor1 Color4 button1 buttonIncial'><h4>Iniciar</h4></Button></Link>
                        </div>
                        </div>
                        <div className="dialogoRodape row center fullWidth distributed3">
                            <Link to="/compartilhar"><Icon className='iconShare' icon='share-alt' size='lg'/></Link>
                            <Link to="/vitorial"><Icon className='iconPg' icon='people-group' size='lg'/></Link>
                        </div>
                    </div>
                </div>
                <div  className='dialogoNav row distributed2'>
Logos dos patrocionadores                
                    
                </div>
                
            </div>
        );
    }
}