//Componente Front-end
import {Alert} from 'rsuite';

export default class ControlGame {
    constructor(palavras, largura, altura) {
        this.largura = largura;
        this.altura = altura;
        this.base_pontos = 500;
        this.pontos = 0;
        this.palavras = palavras;
        this.teste = Array(largura * altura).fill(0);
        this.fim = false;
        this.palavrasCertas = Array(palavras.palavras.length).fill();
        this.refPalavrasCertas = Array(largura * altura).fill(false);
    }

    setAjuda(c){
        this.pontos -= c;
    }

    checkLetra(c) {
        this.teste[c] = this.teste[c] === 0 ? 1 : 0;
        this.palavraLetra(c);

    }

    palavraLetra(c) {
        for (let index = 0; index < this.palavras.posicoes.length; index++) {
            if (this.palavras.posicoes[index].includes(c)) this.acertoPalavra(index);
        }
    }

    acertoPalavra(palavra) {
        let palavraTest = this.palavras.posicoes[palavra];
        let acerto = true;
        for (let index = 0; index < palavraTest.length; index++) {
            if (this.teste[palavraTest[index]] === 0) acerto = false;
        }

        if (acerto === true) this.acerto(palavra);

    }

    acerto(index) {
        Alert.success('Você encontrou '+this.palavras.palavras[index]+', parabens !');
        this.pontos += this.base_pontos;
        for (let ind = 0; ind < this.palavras.posicoes[index].length; ind++) {
            this.refPalavrasCertas[this.palavras.posicoes[index][ind]]=  true;            
        }
        this.palavrasCertas[this.palavras.ref[index]] = true;     
        this.palavras.posicoes  = this.arrayRemove(this.palavras.posicoes, this.palavras.posicoes[index]);       
        this.palavras.palavras  = this.arrayRemove(this.palavras.palavras, this.palavras.palavras[index]);
        this.palavras.ref  = this.arrayRemove(this.palavras.ref, this.palavras.ref[index]);   
        this.vitoria();
    }

    vitoria() {
        if (this.palavras.posicoes.length === 0)
            this.fim = true;
    }

    getRefPalavrasCertas(){
        return this.refPalavrasCertas;
    }

    getPalavrasCertas(){
        return this.palavrasCertas;
    }

    getVitoria(){
        return this.fim;
    }

    getPontos() {
        return this.pontos;
    }

    getAcertos() {
        return this.pontos / this.base_pontos;
    }

    getPalavras(){
        return this.palavras;
    }

    arrayRemove(lista, value) {

        return lista.filter(function (ele) {
            return ele !== value;
        });

    }


}
